import React, { useEffect, useState } from "react";
import { Carousel } from "react-bootstrap";
import getNews from "./newsData";
import "bootstrap/dist/css/bootstrap.css";
import "../css/MainPage.css";

const NewsCarousel = () => {
  const [news, setNews] = useState([]);

  useEffect(() => {
    getNews().then((data) => data && setNews(data));
  }, []);

  return (
    <div className="newsdiv">
      <Carousel>
        {news.map((theArticle) => (
          <Carousel.Item>
            <img
              className="d-block w-100 img-fluid"
              src={theArticle.urlToImage}
              alt={theArticle.title}
            />
            <Carousel.Caption>
              <h2 style={{ fontSize: 4 + "vw" }}>
                <b>
                  <a
                    style={
                      ({ textDecoration: "none" }, { color: "#cfcfcf" }, {})
                    }
                    href={theArticle.url}
                  >
                    {theArticle.title}
                  </a>
                </b>
              </h2>
            </Carousel.Caption>
          </Carousel.Item>
        ))}
      </Carousel>
    </div>
  );
};

export default NewsCarousel;
