import "bootstrap/dist/css/bootstrap.css";
import React, { Component } from "react";
import "../css/MainPage.css";
import queryString from "query-string";
import Navbar from "../components/Navbar";

function numberWithCommas(x: any) {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

class WorldData extends Component {
  state = {
    loading: true,
    json: {
      active: "",
      confirmed: "",
      deaths: "",
      recovered: "",
      new_deaths: "",
      new_confirmed: "",
      mortality: "",
      recovery: "",
    },
    value: "",
  };

  async componentDidMount() {
    const myProps: any = this.props;
    const value = queryString.parse(myProps.location.search);
    const url = "https://az-covid-19-api.herokuapp.com/api?c=" + value.c;
    const response = await fetch(url);
    const data = await response.json();
    this.setState({ json: data, loading: false, value: value.c });
  }

  render() {
    return (
      <div>
        <Navbar />
        <br />
        <h2 className="text-center">
          Coronavirus Live Stats for <b>{this.state.value}</b>
        </h2>

        <br />
        <div className="confirmed center">
          <h2>Total Cases:</h2>
          <h1>
            {numberWithCommas(this.state.json.confirmed)} (+
            {numberWithCommas(this.state.json.new_confirmed)})
          </h1>
        </div>
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <div className="active center">
          <h3>Active Cases:</h3>
          <h1>{numberWithCommas(this.state.json.active)}</h1>
        </div>
        <br />
        <br />
        <br />
        <br />
        <br />
        <div className="deaths center">
          <h3>Total Deaths:</h3>
          <h1>
            {numberWithCommas(this.state.json.deaths)} (+
            {numberWithCommas(this.state.json.new_deaths)})
          </h1>
        </div>
        <br />
        <br />
        <br />
        <br />
        <br />
        <div className="recovered center">
          <h2>Recovered:</h2>
          <h1>{numberWithCommas(this.state.json.recovered)}</h1>
        </div>
      </div>
    );
  }
}

export default WorldData;
