const getNews = async () => {
  const url: string =
    "http://newsapi.org/v2/top-headlines?q=coronavirus&country=us&apiKey=2d93d262007248b18dbbf71961515d74";
  const response: any = await fetch(url).then((data) => data);
  const data: any = await response.json();
  return data.articles;
};

export default getNews;
