import React, { Component } from "react";
import "bootstrap/dist/css/bootstrap.css";
import "../css/MainPage.css";

function numberWithCommas(x: any) {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

class UserCountryData extends Component {
  state = {
    loading: true,
    country: "",
    json: {
      active: "",
      confirmed: "",
      deaths: "",
      recovered: "",
      new_deaths: "",
      new_confirmed: "",
      mortality: "",
      recovery: "",
    },
  };

  async componentDidMount() {
    const ipjson = await (await fetch("https://ipapi.co/json")).json();
    let country_name: any = ipjson.country_name;
    const country_real_name = ipjson.country_name;
    const spaceCountry = [
      "United States",
      "United Kingdom",
      "United Arab Emirates",
    ];
    if (spaceCountry.includes(country_name)) {
      const matches: any = country_name.match(/\b(\w)/g);
      country_name = matches.join("");
    }

    const url = "https://az-covid-19-api.herokuapp.com/api?c=" + country_name;
    const response = await fetch(url);
    const data = await response.json();
    this.setState({ json: data, country: country_real_name, loading: false });
  }

  render() {
    return (
      <div>
        <br />
        <h2 className="text-center">
          Coronavirus Stats for <b>{this.state.country}</b>
        </h2>
        <br />
        <div className="confirmed-u">
          <h2>Total Cases:</h2>
          <h1 className="text-center">
            {numberWithCommas(this.state.json.confirmed)} (+
            {numberWithCommas(this.state.json.new_confirmed)})
          </h1>
        </div>
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <div className="active-u center">
          <h3>Active Cases:</h3>
          <h1 className="text-center">
            {numberWithCommas(this.state.json.active)}
          </h1>
        </div>
        <br />
        <br />
        <br />
        <br />
        <br />
        <div className="deaths-u center">
          <h3>Total Deaths:</h3>
          <h1 className="text-center">
            {numberWithCommas(this.state.json.deaths)} (+
            {numberWithCommas(this.state.json.new_deaths)})
          </h1>
        </div>
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <div className="recovered-u center">
          <h2>Recovered:</h2>
          <h1 className="text-center">
            {numberWithCommas(this.state.json.recovered)}
          </h1>
        </div>
      </div>
    );
  }
}

export default UserCountryData;
